.timeline-header{
    display:flex;
    justify-content: space-between;
    position:sticky;
    top: 0;
    background-color:salmon;
    height:50px;

}

.header-wrapper{
    display:flex;
    
}

.single-post-wrapper{
    height:700px;
    width:725px;
    /* border: 5px solid black; */
    text-align:left;
    display:flex;
    
    align-items:center;
    flex-direction:column;
    margin-bottom: 100px;
    background-color: white;
    border-radius:10px;
}

.post-header-wrapper{
    margin:25px ;
    flex-direction:row;
    /* border: 5px solid black; */
    width: 700px;
}

.header-child-wrapper{
    display:flex;
    /* border: 5px solid blue; */
}



.post-title{
    /* border: 5px solid blue; */
}

